/* *input form */

.todo-input-form {
    text-align: center;
  }
  
  .todo-input {
    font-size: 18px;
    padding: 5px;
    width: 550px;
    max-width: 100%;
    background-color: lightgray;
  }
  
  .todo-input-edit {
    width: 100%;
    color: rgb(30, 30, 164);
    background-color: rgb(238, 238, 238);
  }
  
  .todo-input,
  .todo-input-edit {
    border: 0px;
    border-bottom: 1px solid black;
  }
  
  .todo-input:focus-visible,
  .todo-input-edit:focus-visible {
    outline-style: none;
    outline-width: 0px;
  }